        <div class="carusel">
            <v-carousel
                height="auto"
                :showArrows="showArrows"
                hide-delimiters 
                :cycle="cycle"
            >
                <v-carousel-item
                    v-for="(photo, i) in photos"
                    :key="i"
                >
                    <img class="banner" :src="photo.src">
                </v-carousel-item>
            </v-carousel>
        </div>
        <div class="text-side">
            <div class="container">
                <div class="event-container" v-for="(event, index) in events" :key="index">
                    <h1 class="event-title">{{event.title}}</h1>
                    <p class="event-text">{{event.text}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'main-part',
    props: {
        photos: {
            type: Array,
            default: () => { return []},
            subtype: {
                srs: {
                    type: String,
                    default: ''
                }
            }
        },
        events: {
            type: Array,
            default: () => { return []},
            subtype: {
                title: {
                    type: String,
                    default: ''
                },
                src: {
                    type: String,
                    default: ''
                },
                text: {
                    type: String,
                    default: ''
                }
            }
        },
    },
    data() {
        return {
            showArrows: false,
            cycle: true,
            int: 3
        }
    }
}
</script>

