body {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: #222;
}

.burger-menu {
  width: 64px;
  height: 58px;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;

  .line {
    width: 50%;
    height: 3px;
    background: black;
    margin: 10px;
    overflow: hidden;
    transition: all 0.5s ease;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: lightblue;
      transform: translateX(-100%);
      transition: all 0.25s ease;
    }

    @for $i from 2 through 3 {
      &:nth-child(#{$i})::after {
        transition-delay: 0.1s * ($i - 1);
      }
    }

    &:nth-child(1) {
      transform-origin: top left;
    }

    &:nth-child(2) {
      transform-origin: left;
    }

    &:nth-child(3) {
      transform-origin: bottom left;
    }
  }

  &:hover {
    .line::after {
      transform: translateX(0);
    }
  }

  &.active {
    // box-shadow: 0 0 0 1px lightblue;

    .line {
      &:nth-child(1) {
        transform: translateX(10%) rotate(45deg) scaleX(1.2);

        @include sp-layout {
          transform: rotate(45deg) scaleX(1.6);
        }
      }

      &:nth-child(2) {
        transform: scaleX(0);
      }

      &:nth-child(3) {
        transform: translateX(10%) rotate(-45deg) scaleX(1.2);

        @include sp-layout {
          transform: rotate(-45deg) scaleX(1.6);
        }
      }
    }
  }
}
